// 
// Chat.scss
//

.chat-leftsidebar{
    @media (min-width: 992px) {
      min-width: 380px;
    }
  
  
    .chat-leftsidebar-nav{
      .nav{
        background-color: $card-bg;
      }
  
     
    }
  }
  
  .chat-noti-dropdown{
    &.active{
      &:before{
        content: "";
        position: absolute;
        width: 8px;
        height: 8px;
        background-color: $danger;
        border-radius: 50%;
        right: 0;
      }
    }
  
    .btn{
      padding: 6px;
      box-shadow: none;
      font-size: 20px;
    }
  }
  
  .chat-list{
    margin: 0;
    li{
      &.active{
        a{
          background-color: $card-bg;
          box-shadow: $box-shadow;
        }
      }
      a{
        display: block;
        padding: 14px 16px;
        color: $gray-600;
        transition: all 0.4s;
        border: 1px solid $border-color;
        border-radius: 4px;
        margin-top: 10px;
        &:hover{
          background-color: $card-bg;
          box-shadow: $box-shadow;
  
        }
      }
    }
  }
  
  
  .user-chat-nav{
    .dropdown{
      .nav-btn{
        height: 40px;
        width: 40px;
        line-height: 34px;
        box-shadow: none;
        padding: 0;
        font-size: 16px;
        background-color: $light;
        border-radius: 50%;
      }
  
      .dropdown-menu{
        box-shadow: $box-shadow;
        border: 1px solid $border-color
      }
    }
  }
  
  
  .chat-conversation{
    li{
      clear: both;
    }
  
    .chat-day-title{
      position: relative;
      text-align: center;
      margin-bottom: 24px;
  
      .title{
        background-color: $card-bg;
        position: relative;
        z-index: 1;
        padding: 6px 24px;
      }
  
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0;
        right: 0;
        background-color: $border-color;
        top: 10px;
      }
      .badge{
        font-size: 12px;
      }
    }
    .conversation-list{
      margin-bottom: 24px;
      display: inline-block;
      position: relative;
      .arrow-left{
        position: relative;
        &:before{
          content: "";
       position: absolute;
       top: 10px;
       right: 100%;
       border: 7px solid transparent;
       border-right: 7px solid rgba($primary,0.1);
         }
      }
  
      .ctext-wrap{
        padding: 12px 24px;
        background-color: rgba($primary,0.1);
        border-radius: 8px 8px 8px 0px;
        overflow: hidden;
       
  
        .conversation-name{
          font-weight: 500;
          color: $primary;
          margin-bottom: 4px;
          position: relative;
      
        }
      }
  
      .dropdown{
        float: right;
        .dropdown-toggle{
          font-size: 18px;
          padding: 4px;
          color: $gray-600;
          @media (max-width: 575.98px) {
            display: none;
          }
        }
  
        .dropdown-menu{
          box-shadow: $box-shadow;
          border: 1px solid $border-color
        }
      }
  
      .chat-time{
        font-size: 12px;
      }
    }
  
    .right{
      .conversation-list{
        float: right;
        .arrow-right{
          position: relative;
          &:before{
            content: "";
         position: absolute;
         top: 10px;
         left: 100%;
         border: 7px solid transparent;
         border-left: 7px solid $light;
           }
        }
        .ctext-wrap{
          background-color: $light;
          text-align: right;
          border-radius: 8px 8px 0px 8px;
        }
        .dropdown{
          float: left;
        }
  
        &.last-chat{
          .conversation-list{
            &:before{
              right: 0;
              left: auto;
            }
          }
        }
      }
  
    }
  
  
  }
  
  .chat-input-section{
    border-top: 1px solid $border-color;
  }
  
  .chat-input{
    border-radius: 30px;
    background-color: $light !important;
    border-color:  $light !important;
    padding-right: 120px;
  }
  
  .chat-input-links{
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
    li{
      a{
        font-size: 16px;
        line-height: 36px;
        padding: 0px 4px;
        display: inline-block;
      }
    }
  }
  
  .chat-send{
    @media (max-width: 575.98px) {
      min-width: auto;
    }
  }
  
  
  .search-box .search-icon {
    font-size: 16px;
    position: absolute;
    left: 13px;
    top: 2px;
    font-size: 15px;
    line-height: 34px;
   
  }
  
  .search-box .form-control {
    padding-left: 40px;
    border-radius: 5px;
  }
  
  